@mixin visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@mixin list {
  list-style: none;
  padding: 0;
  margin: 0;

}

@mixin link {
  text-decoration: none;
  color: #FFFFFF;
}

@mixin title {
  font-family: $main-font;
  font-weight: 400;
  margin: 0;
  font-size: 20px;
  line-height: 170%;
  text-transform: uppercase;
  letter-spacing: 1px;
}

@mixin title-tablet{
  font-size: 30px;
  line-height: 33px;
  letter-spacing: 0.06em;
  text-align: center;
}

